import type {StepChangeResult, WizardState} from './wizard'
import {attr, controller, target} from '@github/catalyst'
import type {SinglePageWizardStepContentBase} from './step-content-base'

/**
 * <single-page-wizard-step> is used to render each step of a single page wizard.
 * See the parent wizard.ts (single-page-wizard/wizard.ts) for more details and an example of how to use this component.
 */
@controller
export class SinglePageWizardStepElement extends HTMLElement {
  // targets that control the rendering of the "badge"
  // that is used to render step number (for the last step it's a check mark icon)
  @target stepNumberBadge: HTMLElement
  @target stepError: HTMLElement

  // button targets
  @target cancelButton: HTMLElement
  @target previousButton: HTMLButtonElement
  @target nextButton: HTMLButtonElement
  @target nextButtonSpinner: HTMLElement

  // stepContent is a child catalyst component of the step that implements the SinglePageWizardStepContentBase class
  @target stepContent: SinglePageWizardStepContentBase

  @attr singlePageWizardStep: number
  @attr singlePageWizardStepCurrent: boolean
  @attr singlePageWizardStepComplete: boolean
  @attr singlePageWizardLastStep: boolean

  connectedCallback() {
    const showCancelBtn = this.getAttribute('show-cancel-btn') === 'true'
    const showPreviousBtn = this.getAttribute('show-previous-btn') === 'true'
    const hideNextBtn = this.getAttribute('hide-next-btn') === 'true'

    // the cancel button is hidden by default
    // show it if configured to do so on the step
    if (showCancelBtn) {
      this.cancelButton.hidden = false
    }

    // the previous button is hidden by default
    // show it if configured to do so on the step
    if (showPreviousBtn) {
      this.previousButton.hidden = false
    }

    // the next button is _visible_ by default
    // hide it if configured to do so on the step
    if (hideNextBtn) {
      this.nextButton.hidden = true
    }
  }

  // custom steps can trigger this event using 'single-page-wizard-step-validate'
  // and a 'isValid' detail boolean to indicate whether the step is valid/ready to move to the next step
  handleValidate(event: CustomEvent) {
    this.stepValid(event.detail.isValid, event.detail.autoProceed)
  }

  // custom steps can trigger this event using 'single-page-wizard-step-error'
  // to show/hide an error block below the step content
  handleStepError(event: CustomEvent) {
    if (event.detail?.errorMessage) {
      this.stepError.hidden = false
      this.stepError.textContent = event.detail.errorMessage
    } else {
      this.stepError.hidden = true
    }
  }

  update(step: number, isLastStep: boolean, isCurrentStep: boolean, isComplete: boolean) {
    this.singlePageWizardStep = step
    this.stepNumberBadge?.setAttribute('data-single-page-wizard-step', this.singlePageWizardStep.toString())
    this.singlePageWizardLastStep = isLastStep
    this.singlePageWizardStepCurrent = isCurrentStep
    this.singlePageWizardStepComplete = isComplete
  }

  async setStepState(wizardState: WizardState) {
    this.stepContent && this.stepContent.setStepState(wizardState)
    this.stepValid(this.stepContent ? this.stepContent.isStepValid() : true)
  }

  /* eslint-disable-next-line custom-elements/no-method-prefixed-with-on */
  async onStepRendered() {
    this.stepContent && (await this.stepContent.onStepRendered())
  }

  // handle changes to the step based on whether it's "valid" (meaning it's in proper state to move to the next step)
  stepValid(isStepValid: boolean, autoProceed?: boolean) {
    this.nextButton.disabled = !isStepValid
    if (isStepValid && autoProceed) {
      this.nextButton.click()
    }
  }

  // button action onClick handling
  /* eslint-disable-next-line custom-elements/no-method-prefixed-with-on */
  async onNext(): Promise<void> {
    this.nextButton.disabled = true
    this.nextButtonSpinner.hidden = false
    const {proceed, wizardState} = this.stepContent
      ? await this.stepContent.onNextClick()
      : ({proceed: true} as StepChangeResult<WizardState>)
    if (proceed) {
      this.dispatchEvent(new CustomEvent('single-page-wizard-increment', {bubbles: true, detail: {wizardState}}))
    }
    this.nextButtonSpinner.hidden = true
    this.nextButton.disabled = false
  }

  /* eslint-disable-next-line custom-elements/no-method-prefixed-with-on */
  async onPrevious(): Promise<void> {
    this.previousButton.disabled = true
    const {proceed, wizardState} = this.stepContent
      ? await this.stepContent.onPreviousClick()
      : ({proceed: true} as StepChangeResult<WizardState>)
    if (proceed) {
      this.dispatchEvent(new CustomEvent('single-page-wizard-decrement', {bubbles: true, detail: {wizardState}}))
    }
    this.previousButton.disabled = false
  }

  /* eslint-disable-next-line custom-elements/no-method-prefixed-with-on */
  onCancel() {
    this.parentElement?.dispatchEvent(
      new CustomEvent('single-page-wizard-step-cancel', {bubbles: true, detail: {step: this.singlePageWizardStep}}),
    )
  }
}
