import {attr, controller, target, targets} from '@github/catalyst'
import {SinglePageWizardStepElement} from './step'
import celebration from '../behaviors/confetti'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

export type WizardState = unknown

export type StepChangeResult<T> = {
  proceed: boolean
  wizardState?: T
}

/**
 * <single-page-wizard> provides a numerically stepped wizard UI implemented
 * as a Catalyst component and a single page flow, managing presentation/visibility
 * of each active step.
 *
 * <single-page-wizard-step> is used to render each step of the wizard.
 *
 * All <single-page-wizard-step> should be rendered as direct
 * children of <single-page-wizard>. Each wizard step will then yield the block
 * which renders the step implementation. A step should be implemented with <single-page-wizard-step-content>
 * for plain html that doesn't require validation, or more realistically via custom catalyst component
 * which extends SinglePageWizardStepContentBase (see app/assets/modules/github/single-page-wizard/step-content-base.ts).
 *
 * SinglePageWizardStepContentBase provides a basic interfaces for
 * validation and step nav event handling.
 *
 * == Events
 * - single-page-wizard-cancel: Fires when the user clicks cancel on any step (if cancel-url was _not_ provided)
 * - single-page-wizard-complete: Fires when the user clicks the "next" button on the last step (if complete-url was _not_ provided)
 * - single-page-wizard-step-change: Fires when the user navigates to the next or previous step.
 *                                   Event detail includes the current step number (1 based).
 *
 * See SinglePageWizard::WizardComponent (single_page_wizard/wizard_component.rb) for usage.
 */
@controller
class SinglePageWizardElement extends HTMLElement {
  @targets steps: SinglePageWizardStepElement[]
  @target loadingElem: HTMLElement
  @target stepsElem: HTMLElement
  @attr horizontalSteps: boolean
  @targets horizontalBadges: HTMLElement[]
  @targets horizontalBadgeChecks: HTMLElement[]
  @targets horizontalBars: HTMLElement[]
  @attr currentStep = 1

  // holds arbitrary state for the entire wizard that is passed down to individual steps to reference
  wizardState: WizardState = {}

  connectedCallback() {
    // the single-page-wizard element is hidden by default
    // it is shown once all children have registered that they are loaded
    let childrenLoaded = 0
    observe('.wizard-step', {
      constructor: SinglePageWizardStepElement,
      add: async () => {
        childrenLoaded++
        if (childrenLoaded === this.steps.length) {
          await this.updateSteps()
          this.stepsElem.hidden = false
          this.loadingElem.hidden = true
        }
      },
    })
  }

  // when the current step on the wizard changes
  // we need to change up the styles on each step
  async incrementStep(event: CustomEvent) {
    if (this.currentStep === this.steps.length) {
      const completeUrl = this.getAttribute('complete-url')
      if (completeUrl) {
        window.location.href = completeUrl
      } else {
        this.dispatchEvent(new CustomEvent('single-page-wizard-complete', {bubbles: true}))
      }
      return
    }
    this.currentStep += 1
    const showLastStepCelebration = this.getAttribute('show-last-step-celebration') === 'true'
    if (this.currentStep === this.steps.length && showLastStepCelebration) {
      celebration()
    }
    await this.handleStepChange(event.detail.wizardState)
  }

  // when the current step on the wizard changes
  // we need to change up the styles on each step
  async decrementStep(event: CustomEvent) {
    this.currentStep -= 1
    await this.handleStepChange(event.detail.wizardState)
  }

  async handleStepChange(wizardState: WizardState) {
    if (wizardState) {
      this.wizardState = wizardState
    }
    this.dispatchEvent(
      new CustomEvent('single-page-wizard-step-change', {
        bubbles: true,
        detail: {
          currentStep: this.currentStep,
          wizardState: this.wizardState,
        },
      }),
    )

    await this.updateSteps()
  }

  // emits a bubbled event for cancel that can be captured by a
  // wrapping component if the event needs to be handled in a specific way
  stepCancel(event: CustomEvent) {
    const cancelUrl = this.getAttribute('cancel-url')
    const confirmText = this.getAttribute('cancel-confirm-text')

    if (cancelUrl) {
      if (confirmText) {
        const step = this.steps[event.detail.step - 1]
        if ((step && step.getAttribute('warn-unsaved') === 'true') || confirm(confirmText)) {
          window.location.href = cancelUrl
        }
      } else {
        window.location.href = cancelUrl
      }
    } else {
      this.dispatchEvent(new CustomEvent('single-page-wizard-cancel', {bubbles: true, detail: event.detail}))
    }
  }

  // updateSteps is used whenever state changes on the wizard that requires changes to each step
  async updateSteps() {
    await this.steps[this.currentStep - 1]!.setStepState(this.wizardState)

    if (this.horizontalSteps) {
      this.updateHorizontalStepIndicators()
    }

    for (let i = 0; i < this.steps.length; i++) {
      const step = i + 1
      const isLastStep = i === this.steps.length - 1
      const isCurrent = this.currentStep === i + 1

      if (isCurrent) {
        if (this.steps[i]!.getAttribute('warn-unsaved') === 'true') {
          // This message is ignored by most browsers.
          // eslint-disable-next-line i18n-text/no-en
          const confirmText = this.getAttribute('cancel-confirm-text') || 'Changes you made may not be saved.'
          window.onbeforeunload = function (event: BeforeUnloadEvent): string | undefined {
            // This is sort of a hack to allow elements of the wizard to ignore the warning in certain cases.
            if (document.getElementById('one-off-ignore-single-page-wizard-warning')) {
              const elem = document.getElementById('one-off-ignore-single-page-wizard-warning')
              elem?.parentNode?.removeChild(elem)
              return
            }
            event.returnValue = confirmText
            return confirmText
          }
        } else {
          window.onbeforeunload = null
        }
      }

      const isComplete = this.currentStep > i + 1
      this.steps[i]!.update(step, isLastStep, isCurrent, isComplete)
    }

    await this.steps[this.currentStep - 1]!.onStepRendered()
  }

  // Horizontal layout hides the regular vertical step indicators in favor of a separate div above the steps.
  // There are indicators w/ numbers (displayed for current and future steps),
  // and badges with checkmarks (displayed for completed steps) - these are separate divs for each step and
  // numbers vs checkmarks are conditionally hidden/shown and colored based on progress through the wizard.
  // The entirety of non-active steps is also hidden, since just hiding content still shows whitespace of the container.
  updateHorizontalStepIndicators() {
    for (let currIndex = 0; currIndex < this.steps.length; currIndex++) {
      const badge = this.horizontalBadges[currIndex]!
      const badgeCheck = this.horizontalBadgeChecks[currIndex]!
      const bar = this.horizontalBars[currIndex]
      const step = this.steps[currIndex]!

      if (currIndex + 1 === this.currentStep) {
        badge.classList.add('current')
        step.removeAttribute('hidden')

        // remove complete, in case user went forward (marking complete) and then came back
        badge.classList.remove('complete')
        badgeCheck.classList.remove('complete')
        bar?.classList.remove('complete')

        // last step should change title, since it's a confirmation step
        if (currIndex + 1 === this.steps.length) {
          document.querySelector('#wizard-title-in-progress')?.setAttribute('hidden', 'true')
          document.querySelector('#wizard-title-complete')?.removeAttribute('hidden')
        }
      } else {
        badge.classList.remove('current')
        step.setAttribute('hidden', 'true')

        if (currIndex + 1 < this.currentStep) {
          badge.classList.add('complete')
          badgeCheck.classList.add('complete')
          bar?.classList.add('complete')
        }
      }
    }
  }
}
